<template>
    <div class="w-full h-full fixed block top-0 left-0 bg-white opacity-75 z-50" v-if="loaderTrigger">
      <div class="text-green-500 opacity-75 top-1/2 my-0 mx-auto block relative h-50" style="
        top: 40%;
    ">
        <img src="@/assets/blue_loading.gif" class="mx-auto block h-50" alt="Loading...">
      </div>
    </div>

    <div class="card lg:card-side bg-gray-200 shadow-xl w-full h-full lg:mr-12 rounded-3xl lg:ml-12 md:h-85vh">
      <div class="card-body">
        <div class="tabs tabs-boxed bg-gray-200 rounded-xl">
          
          <a class="tab" v-bind:class="{ 'bg-gray-300 font-bold text-blue-600 rounded-xl': selected_book === book }" v-for="book in books" :key="book.key" v-on:click="selectBook(book)">{{book}}</a> 
        </div>

        <div class="">
            <div class="w-full border-t border-gray-300"></div>
        </div>

        <div class="sm:flex sm:justify-between sm:flex mb-2 items-center">
          <div class="text-left">
            <p class="text-xl font-bold">{{selected_book}} </p>
            <p class="text-xs ">{{selected_book_desc}}</p>
          </div>
          <div class="sm:mt-2">
            <input type="text" class="h-8 w-50 pr-8 pl-5 rounded-xl z-0 focus:shadow focus:outline-none" placeholder="Search in...">
          </div>
        </div>
        <div class="grid lg:grid-cols-6 grid-cols-5 xs:grid-cols-2 lg:gap-2 h-full rounded-3xl">
          <div class="bg-base-100 rounded-box hidden lg:block rounded-3xl md:h-65vh">
            <p class="text-left font-bold p-6" v-if="active_tile == null">Pages</p>
            <p class="text-left font-bold p-6" v-if="active_tile != null"><a v-on:click="resetTile" class="hover:opacity-25 hover:cursor-pointer"><img src="@/assets/left-caret.png" class="h-4 inline">&emsp;{{this.local.selectedPage}}</a></p>
            <ul class="menu bg-base-100 menu-compact p-2 w-full overflow-y-auto md:h-45vh lg:h-50vh"  v-if="!active_tile">
              <li v-for="page in active_pages" :key="page.key" v-bind:class="{ 'bg-gray-100 font-bold text-blue-500': selected_page === page.title }" v-on:click="selectPage(page.title)"><a>{{page.title}}</a></li>
             </ul>
              <ul class="menu bg-base-100 menu-compact p-2 w-full overflow-y-auto md:h-45vh lg:h-50vh" id="tileScrollable" v-if="active_tile != null">
              <li v-for="tile in showTile" :key="tile.key" 
                  v-bind:class="{ 'bg-gray-100 font-bold text-blue-500': active_tile.tile.tile.title === tile.tile.title }" 
                  v-on:click="tile_selected({tile})"
                  v-bind:id="'tile'+tile.tile.id">
                  <a>{{tile.tile.title}}</a>
              </li>
             </ul>
          </div>
          <div class="col-span-5 bg-gray-200 px-5 lg:ml-5 md:overflow-y-auto md:h-60vh hover:cursor-pointer lg:h-65vh"  v-if="active_tile == null" >
            <div class="flex flex-wrap -mx-2 ">

              <div class="my-1 px-2 w-full overflow-hidden sm:3/6  md:w-1/6 xl:w-1/6 rounded-3xl" v-for="tile in showTile" :key="tile.key">
                <div class="bg-base-100 rounded-box hover:bg-blue-100 py-10 mb-2 rounded-3xl" 
                v-on:click="tile_selected({tile})">
                        <img :src="'https://staging-tap3.cloudstaff.com/img/icons/'+tile.tile.icon" class="mx-auto h-10"/>
                        <div class="py-1 text-base flex justify-between">
                            <p class="text-xs font-bold text-gray-900 opacity-75 p-2">{{tile.tile.title}}</p>
                        </div>
                </div>
              </div>
            </div>
          </div>
          <div class="col-span-5 bg-base-100 px-5 lg:ml-5 md:overflow-y-auto md:h-60vh lg:h-65vh rounded-3xl"  v-if="active_tile != null" >
            <!-- TILE CONTENT -->
          </div>
        </div>
      </div>
    </div>
    
</template>



<script>

import { mapGetters } from 'vuex';
import SecureLS from  'secure-ls'
var ls = new SecureLS({encodingType: "aes", encryptionSecret:"tap4web" ,isCompression: true})

export default {
 
  data() {
          return {
            local: {},
            books: [],
            pages: [],
            tiles: [],
            bookFilter: [],
            loaderTrigger: false
        }
      },
  computed:{
    ...mapGetters(['authenticated',
                   'auth_token',
                   'auth_user',
                   'selected_book',
                   'active_pages',
                   'selected_page',
                   'selected_book_desc',
                   'loading',
                   'active_tile'
                  ]),
    showTile: function (){
        if (this.tiles) {
            return this.tiles.filter(item => {
                return item.tile.page_tile === this.$store.getters.selected_page;
          });
        } else {
          return "";
        }
          
    }
  },
  mounted(){
        if (localStorage.vuex) {
        this.local = JSON.parse(ls.get('vuex'));

        this.local.books.map((item, index) => {
              if (index === 0) {
                  if (this.$store.getters.selected_book == ""){    
                      this.$store.dispatch('activeBook', item.code);
                      this.$store.dispatch('activeBookDesc', item.description);
                      this.$store.dispatch('setPages', item.pages);
                  }
                  this.books.push(item.code);
              } else {
                  this.books.push(item.code);
              }
        });

        console.log(JSON.stringify(this.$store.getters.active_pages));   
        
        this.$store.getters.active_pages.map((item, index) => {
            console.log(index);
            if (index === 0) {
                if (this.$store.getters.selected_page == "") {    
                      this.$store.dispatch('activePage', item.title);
                  }
            }
            item.tiles.map((tile, tile_index) => {
                console.log("tile index="+tile_index);
                this.tiles.push({tile});
            });
        });

        if (this.$store.getters.active_tile != null) {
            setTimeout(() => {
              this.scrollFunc(this.$store.getters.active_tile.tile.tile.id);
            }, 1000);
        }
        

        
    } 
  },
  methods: {
        resetTile () {
            this.$store.dispatch('unsetActiveTile');
        },
        async selectBook (book) {
              this.loaderTrigger = true;
          
              await new Promise(resolve => {
                    setTimeout(() => {
                      this.$store.dispatch('activeBook', book);
                      this.$store.dispatch('unsetActiveTile');
                      this.bookFilter = this.local.books.filter(item =>{
                          return item.code == book
                        });

                        this.bookFilter.map( (item) => {
                          this.$store.dispatch('activeBookDesc', item.description);
                          this.$store.dispatch('setPages', item.pages);

                          this.tiles = [];
                          item.pages.map( (page_item, index) => {
                                if (index === 0) {   
                                          this.$store.dispatch('activePage', page_item.title);
                                }
                                page_item.tiles.map( (tile) => {
                                      this.tiles.push({tile});
                                  });
                          });
                        });
                        resolve(1);
                    }, 1);
              });
              this.loaderTrigger = false;
        },

        async selectPage (page) {
            this.loaderTrigger = true;
            await new Promise(resolve => {
              setTimeout(() => {
              this.$store.dispatch('activePage', page);
              resolve(1);
              }, 1);
            });
            this.loaderTrigger = false;
        },

        tile_selected (tile_options) {
            this.$store.dispatch('activeTile', tile_options);
            setTimeout(() => {
                this.scrollFunc(tile_options.tile.tile.id)
            }, 1000);
        },

        async scrollFunc (element) {
          var options = {
                easing: 'ease', 
                cancelable: false,
                container: '#tileScrollable',
            }

          this.$scrollTo('#tile'+element, 1000, options)
        } 
      }
    
}
</script>
