import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './index.css'
import moshaToast from 'mosha-vue-toastify'
import 'mosha-vue-toastify/dist/style.css'
import vueScrollTo from 'vue-scrollto'

const app = createApp(App);


var baseURL = window.location.origin;

if(baseURL.includes("staging") || baseURL.includes("local")){
        app.config.globalProperties.baseAPIURL = 'https://staging-tap3.cloudstaff.com';
}else{
        app.config.globalProperties.baseAPIURL = 'https://tap3.cloudstaff.com';
}



app.use(store).use(moshaToast).use(vueScrollTo).use(router).mount('#app')
