import { createStore } from 'vuex'
import axios from 'axios';

import createPersistedState from 'vuex-persistedstate'
import SecureLS from  'secure-ls'
var ls = new SecureLS({encodingType: "aes", encryptionSecret:"tap4web" ,isCompression: true})

const getDefaultState = () => {
  return {
    currentUser: "",
    currentToken: "",
    currentGuid: "",
    books: {},
    addtlBooks: {},
    selectedBook: "",
    selectedBookDesc: "",
    selectedPage: "",
    activePages: {},
    activeTileContent: null,
    loading: "notloading"
  }
}

export default createStore ({
  state: getDefaultState(),
  plugins: [ createPersistedState({
    paths: [
            'currentUser',
            'currentToken',
            'currentGuid',
            'books',
            'addtlBooks',
            'selectedBook',
            'activePages',
            'selectedPage',
            'selectedBookDesc',
            'activeTileContent'
    ],
    storage: {
      getItem: (key) => ls.get(key),
      setItem: (key, value) => ls.set(key, value),
      removeItem: (key) => ls.remove(key)
    }
  })],

  getters: {
    authenticated (state) {
      console.log(state.currentToken);
      if (
          state.currentToken != "" &&
          state.currentUser != "" && 
          state.currentGuid != ""
        ) {
          return true
        }
      return false
    },
    auth_token (state) {
      return state.currentToken;
    },
    auth_user (state) {
      return state.currentUser;
    },
    selected_book (state) {
      return state.selectedBook;
    },
    selected_book_desc (state) {
      return state.selectedBookDesc;
    },
    selected_page (state) {
      return state.selectedPage;
    },
    active_pages (state) {
      return state.activePages;
    },
    active_tile (state) {
      return state.activeTileContent;
    },
    books (state) {
      return state.books;
    },
    addtlBooks(state) {
      return state.addtlBooks;
    },
    loading(state) {
      return state.loading;
    }
  },
  mutations: {
    SET_CURRENT_USER (state, user) {
      state.currentUser = user;
    },
    SET_CURRENT_TOKEN (state, token) {
      state.currentToken = token;
    },
    SET_CURRENT_GUID (state, guid) {
      state.currentGuid = guid;
    },
    // SET_CURRENT_BOOKS (state, books) {
    //   state.books.push (books)
    // },
    SET_CURRENT_BOOKS (state, books) {
      state.books = books
    },

    SET_SELECTED_BOOK (state, payload) {
      state.selectedBook = payload
    },

    SET_SELECTED_BOOK_DESC (state, payload) {
      state.selectedBookDesc = payload
    },

    SET_SELECTED_PAGE (state, payload) {
      state.selectedPage = payload
    },

    SET_CURRENT_PAGES (state, payload) {
      state.activePages = payload
    },

    SET_ACTIVE_TILE (state, payload) {
      state.activeTileContent = payload
    },

    UNSET_ACTIVE_TILE (state) {
      state.activeTileContent = null
    },

    //TEST
    ADD_BOOK (state, payload) {
      state.addtlBooks = payload
    },
    //TEST

    REMOVE_CURRENT_AUTH (state) {
      ls.removeAll();
      localStorage.removeItem('vuex');
      Object.assign(state, getDefaultState());
    },

    SET_LOADING (state, payload) {
      state.loading = payload
    }
   
  },
  actions: {
    addBook (_, payload) {
      ls.set('book_'+payload.code, payload);
    },

    setLoading ({commit}, payload) {
        commit('SET_LOADING', payload)
    },

    activeBook ({commit}, payload) {
        commit("SET_SELECTED_BOOK", payload)
    },

    activeTile ({commit}, payload) {
      return new Promise((resolve, reject) => {
        commit("SET_ACTIVE_TILE", payload)
        if (payload != null) {
          resolve("success");
        } else {
          reject("error");
        }
      });
    },

    unsetActiveTile ({commit}) {
      commit('UNSET_ACTIVE_TILE');
    },

    activeBookDesc ({commit}, payload) {
      commit("SET_SELECTED_BOOK_DESC", payload)
    },

    activePage ({commit}, payload) {
      return new Promise((resolve, reject) => {
        commit("SET_SELECTED_PAGE", payload)
        if (payload != null) {
          resolve("success");
        } else {
          reject("error");
        }
      });
    },

    setPages ({commit}, payload) {
      commit("SET_CURRENT_PAGES", payload)
    },
   
    logoutUser ({commit}) {
      commit('REMOVE_CURRENT_AUTH');
    },
    signIn ({commit}, payload) {
      var bodyFormData = new FormData();

      bodyFormData.append('code', payload.code);
      bodyFormData.append('email', payload.email);
      bodyFormData.append('device_identifier', payload.device_identifier);
      bodyFormData.append('device_type', payload.device_type);
      bodyFormData.append('fcm_token', payload.fcm_token);

      return new Promise ((resolve, reject) => {
        // Do something here... lets say, a http call using vue-resource
        axios ({
              method: "post",
              url: payload.baseAPIURL+"/apiv2/confirm.json",
              data: bodyFormData,
              headers: { 
                "Content-Type": "multipart/form-data",
              },
            })
            .then(response => {
              //handle success
              console.log("VERIFY RESPONSE: "+response.data);

              if (response.data.result == "success") {
                
                  commit('SET_CURRENT_USER', payload.email);
                  commit('SET_CURRENT_TOKEN', response.data.token);
                  commit('SET_CURRENT_GUID', response.data.user_guid);
                  commit('SET_CURRENT_BOOKS', response.data.data.book_code);

                  resolve(response);
              } else {
                  reject(response);
              }

            })
            .catch(response => {
              //handle error
              console.log("ERROR VERIFY RESPONSE: "+response);
              reject(response);
            });
      })
    },
   

  },
  modules: {
   
  }
})
