                                <div id="otp" class="flex flex-row justify-center text-center px-2 mt-5"> 
                                  <input class="m-2 border h-10 w-10 text-center form-control rounded" type="text" v-model="code1" v-on:input="codeChange($event)" @keyup.delete="handleDelete()" id="first"  ref="first"  maxlength="1" /> 
                                  <input class="m-2 border h-10 w-10 text-center form-control rounded" type="text" v-model="code2" v-on:input="codeChange($event)" @keyup.delete="handleDelete()" id="second" ref="second"  maxlength="1" /> 
                                  <input class="m-2 border h-10 w-10 text-center form-control rounded" type="text" v-model="code3" v-on:input="codeChange($event)" @keyup.delete="handleDelete()" id="third"  ref="third"  maxlength="1" /> 
                                  <input class="m-2 border h-10 w-10 text-center form-control rounded" type="text" v-model="code4" v-on:input="codeChange($event)" @keyup.delete="handleDelete()" id="fourth" ref="fourth"  maxlength="1" /> 
                                </div>

