</style>

<script>
import axios from 'axios';

export default {
    data: function () {
        return {
            registerBtn: "Log in",
            verifyBtn: "Verify",
            resendBtn: "Resend OTP",
            changeBtn: "Change email address",
            disabledClass: "",
            disabledChange: "",
            isActive: false,
            email: "",
            errors: [],
            registered: false,
            registered_email: "",
            code1: "",
            code2: "",
            code3: "",
            code4: "",
            code: "",
            device_identifier: "1231",
            device_type: "ios",
            fcm_token: "7155",
            consent: false,
        };
    },
    mounted() {
        console.log("Mounted");
    },
    inject: ["moshaToast"],
    methods: {
        codeChange (event) {
            console.log(event.inputType);
            if (event.inputType === "deleteContentForward" || event.inputType === "deleteContentBackward") {
                if (event.currentTarget.id === "second") {
                    this.$refs.first.focus();
                }
                else if (event.currentTarget.id === "third") {
                    this.$refs.second.focus();
                }
                else if (event.currentTarget.id === "fourth") {
                    this.$refs.third.focus();
                }
                return event.preventDefault();
            }
            else {
                if (event.currentTarget.id === "first") {
                    this.$refs.second.focus();
                }
                else if (event.currentTarget.id === "second") {
                    this.$refs.third.focus();
                }
                else if (event.currentTarget.id === "third") {
                    this.$refs.fourth.focus();
                }
            }
        },
        register (e) {
            e.preventDefault(); // it prevent from page reload
            this.isActive = true; //disable the button
            this.registerBtn = "Loading...";
            this.disabledClass = "opacity-50 cursor-not-allowed";
            var bodyFormData = new FormData();
            bodyFormData.append("email_address", this.email);
            axios({
                method: "post",
                url: this.baseAPIURL+"/apiv2/register",
                data: bodyFormData,
                headers: {
                    "Content-Type": "multipart/form-data"
                },
            })
            .then(response => {
                //handle success
                if (response.data.result === "success") {
                    this.registered = true;
                    this.registered_email = this.email;
                    this.isActive = false; //enable the button
                    this.disabledClass = "";
                    this.registerBtn = "Log in";
                    this.moshaToast("We sent you an OTP! Check it out!", { type: "success" });
                }
                else {
                    this.isActive = false; //enable the button
                    this.disabledClass = "";
                    this.registerBtn = "Log in";
                    this.moshaToast("Invalid email address!", { type: "danger" });
                }
            })
            .catch(response => {
                //handle error
                console.log("error: " + response);
                this.isActive = false; //enable the button
                this.disabledClass = "";
                this.registerBtn = "Log in";
                this.moshaToast("There's an error.", { type: "danger" });
            });
        },
        verify (e) {
            e.preventDefault(); // it prevent from page reload
            this.isActive = true; //disable the button
            this.disabledClass = "opacity-50 cursor-not-allowed";
            this.disabledChange = "opacity-50 cursor-not-allowed";
            this.verifyBtn = "Loading...";
            this.disabledResend = "bg-grey-200 text-white opacity-50 cursor-not-allowed";
            this.code = this.code1 + this.code2 + this.code3 + this.code4;
            this.$store.dispatch("signIn", {
                "code": this.code,
                "email": this.email,
                "device_identifier": this.device_identifier,
                "device_type": this.device_type,
                "fcm_token": this.fcm_token,
                "baseAPIURL": this.baseAPIURL
            })
            .then(response => {
                console.log(response);
                this.isActive = false; //disable the button
                this.disabledClass = "";
                this.disabledResend = "";
                this.verifyBtn = "Verify";
                this.moshaToast("Welcome!", { type: "success" });
                this.$router.push("/dashboard");
            }, error => {
                console.log(error);
                this.isActive = false; //disable the button
                this.disabledClass = "";
                this.disabledResend = "";
                this.verifyBtn = "Verify";
                this.moshaToast("Oops! Incorrect OTP, check your input or resend an OTP!", { type: "danger" });
            });
        },
        resend_code (e) {
            e.preventDefault(); // it prevent from page reload
            var bodyFormData = new FormData();
            bodyFormData.append("email_address", this.email);
            this.isActive = true; //disable the button
            this.disabledClass = "opacity-50 cursor-not-allowed";
            this.resendBtn = "Sending...";
            this.disabledChange = "opacity-50 cursor-not-allowed";
            axios({
                method: "post",
                url: this.baseAPIURL+"/apiv2/resendcode.json",
                data: bodyFormData,
                headers: {
                    "Content-Type": "multipart/form-data",
                },
            })
            .then(response => {
                //handle success
                console.log(response.data);
                this.isActive = false; //disable the button
                this.disabledClass = "";
                this.disabledChange = "";
                this.code = "";
                this.code1 = "";
                this.code2 = "";
                this.code3 = "";
                this.code4 = "";
                this.resendBtn = "Resend OTP";
                this.moshaToast("We resend an OTP! Check it out.", { type: "success" });
            })
            .catch(response => {
                //handle error
                console.log(response);
                this.resendBtn = "Resend OTP";
                this.moshaToast("Oops! Something went wrong.", { type: "danger" });
            });
        },
        change_email (e) {
            e.preventDefault();
            this.registered = false;
            this.email = "";
            this.registered_email = "";
            this.code = "";
            this.code1 = "";
            this.code2 = "";
            this.code3 = "";
            this.code4 = "";
        }
    },
}

</script>
